@import "null";
@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Varela+Round&display=swap');
@import url("https://use.typekit.net/jvp0igl.css");

ol,
ul {
  margin-bottom: 0;
}

body {
  font-family: 'Varela Round', sans-serif;
  font-size: 18px;
  background: lightgray;
}

.header {
  &__wrap {
    background: #de5d5d;
    padding-bottom: 33px;
  }

  &__row {
    display: flex;
    justify-content: space-between;
    padding-top: 35px;
  }

  &__logo {
  }

  &__menu {
    z-index: 1;
    position: relative;
    display: flex;

    ul {
      color: #ffffff;
      font-size: 18px;
      display: flex;
      align-self: center;
      gap: 43px;

      li {
        position: relative;
        cursor: pointer;

        &:hover {
          &:before {
            content: "";
            position: absolute;
            width: 82px;
            top: -42px;
            left: 50%;
            display: flex;
            transform: translateX(-50%);
            border-top: 6px solid #ffffff;
          }
        }
      }
    }
  }
}

.container {
  max-width: 1170px;
  padding-right: 0;
  padding-left: 0;
}

.content {
  &__banner-wrap {
    position: relative;
    max-width: 100%;
    height: fit-content;
    background-color: #de5d5d;
    min-width: 0;
  }

  &__banner-slide {
    height: 100%;
    position: relative;
    z-index: 0;
    min-width: 0;

    .slick-track {
      display: flex;

      .slide__item {
        transition: 1s;
        opacity: 1;

        img {
        }
      }

      .slick-active {
        opacity: 1;
      }
    }

    .slick-list {
      overflow: hidden;

      img {
        max-width: 100% !important;
        display: block;
        margin: 0 auto;
        padding-left: 15px;
      }
    }

    .slick-dots {
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      bottom: 56px;
      font-size: 0;
      transform: translateX(-50%);
      left: calc(50% + 4px);

      li {
        list-style: none;
        margin-right: 8px;
      }

      .slick-active {
        button {
          width: 12px;
          height: 12px;
          background: #ffffff;
          border: 1px solid #ffffff;
          border-radius: 50%;
        }
      }

      button {
        width: 12px;
        height: 12px;
        background: transparent;
        border: 1px solid #ffffff;
        border-radius: 50%;
      }
    }
  }

  &__banner-row {
    margin-top: -689px;
    z-index: 1;
    width: 100%;
    height: 689px;
    display: flex;
    flex-direction: column;
    align-self: center;

    h2 {
      z-index: 1;
      letter-spacing: -1.3px;
      margin-top: 198px;
      margin-right: 10px;
      white-space: nowrap;
      max-width: 839px;
      height: 85px;
      color: #ffffff;
      font-family: Montserrat;
      font-size: 90px;
      font-weight: 600;
      align-self: center;
      margin-bottom: 91px;
    }


  }

  &__banner-buttrow {
    align-self: center;
    margin-left: -5px;
  }

  &__why-wrap {
    background-color: white;

  }

  &__why-row {
    display: flex;
    flex-direction: column;
    gap: 64px;
    padding-top: 100px;
    margin-bottom: 24px;

    h3 {
      padding-left: 3px;
      letter-spacing: -1px;
      align-self: center;
      color: #3a3a3a;
      font-family: Montserrat;
      font-size: 48px;
      font-weight: 600;
    }
  }

  &__why-itemrow {
    display: flex;
    flex-direction: row;
    margin-bottom: 89px;
    justify-content: space-between;
    padding: 0 60px;
  }

  &__why-item {
    display: flex;
    flex-direction: column;

    &:first-child {
