      }
    }


    &__why-row {
      padding: 0;
      padding-top: 20px;
      gap: 20px;

      h3 {
        font-size: 30px;
      }
    }

    &__why-itemrow {
      padding: 0;
      gap: 40px;
      flex-wrap: wrap;
      margin-bottom: 0;
    }

    &__why-item {
      margin: 0 auto;
    }

    &__share-wrap {
      padding-top: 20px;
    }

    &__share-row {
      padding: 0;
      gap: 0;
      flex-direction: column;

      img {
        order: 2;
        width: 70%;
        align-self: center;
      }
    }

    &__share-block {
      padding: 0px 20px;
      max-width: 100%;
      margin: 0;
    }

    &__share-part1 {
      span {
        line-height: 40px;
      }
    }


    &__share-slider {
      .slick-dots {
        justify-content: center;
      }

      .slick-track {
        .item-block {
          flex-direction: column;

          div {
            margin: 0 auto;
            text-align: center;
          }

          img {
            width: fit-content;
          }
        }
      }
    }

    &__features-wrap {
      padding-top: 30px;
    }

    &__features-row {
      img {
        width: 70%;
        align-self: center;
        padding: 0;
      }

      padding: 0;
      flex-direction: column;
    }

    &__features-block {
      padding: 0px 5px;
      align-self: center;
      max-width: 100%;
      gap: 0;
    }


    &__features-part2 {
      img {
        width: fit-content;
      }

      margin: 0;
      align-self: center;
      scale: 0.65;
    }


    &__taken-wrap {
      padding-bottom: 20px;
    }

    &__taken-row {
      padding-top: 20px;

      .container {
        p {
          max-width: 100%;
          text-align: center;
          margin: 0 auto;
          padding: 0;

          span {
            line-height: 35px;
          }
        }
      }
    }

    &__taken-slide {
      max-height: fit-content;
      flex-wrap: wrap;
      gap: 10px;
      padding: 0px 10px;
    }

    &__taken-photo {
      width: calc(100% - 5px);

      &:hover {
        margin: 0;
      }
    }


    &__download-row {
      justify-content: space-between;
      min-height: 100%;
      padding: 10px 10px;

      img {
        max-width: 35%;
        height: fit-content;
        min-height: 100%;
        order: 2;
        align-self: center;
      }
    }

    &__download-block {
      max-width: 70%;
    }

    &__download-part1 {
      p {
        font-size: 20px;
        line-height: 25px;
      }
    }


    &__cimpatibility-row {
      padding: 30px 10px;
    }

    &__cimpatibility-block {
      gap: 50px;
      flex-direction: column;
    }

    &__cimpatibility-part1 {
      width: 100%;
      margin: 0;
      padding: 0;

      &:last-child {
        p {
          margin: 0;
        }

        img {
          width: 100%;

          margin: 0;
        }
      }
    }
  }


  .butt-row {
    &__button {
      &:first-child {
        padding: 20px 5px 20px 32px;

        &:before {
          top: 3px;
          left: -9px;
          content: "";
          scale: 0.5;
        }
      }

      &:last-child {
        padding: 20px 5px 20px 32px;

        &:before {
          top: 5px;
          left: -3px;
          content: "";
          scale: 0.5;
        }
      }

      font-size: 5vw;
    }
  }


  .footer {
    &__row {
      padding: 0px 10px;
      gap: 0;
      flex-direction: column;
      justify-content: space-between;
    }


    &__block-part {
      flex-direction: column;
    }
  }
}

